import { useForm } from "antd/es/form/Form";
import { Button, Form, Input, Modal } from "antd";
import { useEffect } from "react";
import { useDeleteTask, useEditTask } from "utils/task";
import { useTaskModal, useTasksQueryKey } from "./util";
import { UserSelect } from "components/user-select";
import { TaskTypeSelect } from "components/task-type-select";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
export const TaskModal = () => {
  const [form] = useForm();
  const { editingTask, editingTaskId, close } = useTaskModal();
  const { mutateAsync: edit, isLoading: editLoading } = useEditTask(
    useTasksQueryKey()
  );
  const { mutate: deleteTask } = useDeleteTask(useTasksQueryKey());
  const onCancel = () => {
    close();
    form.resetFields();
  };

  const onOk = async () => {
    await edit({ ...editingTask, ...form.getFieldsValue() });
    close();
  };

  const startDelete = () => {
    close();

    Modal.confirm({
      okText: "确定",
      cancelText: "取消",
      title: "确定删除任务吗",
      onOk() {
        return deleteTask({ id: Number(editingTaskId) });
      },
    });
  };

  useEffect(() => {
    form.setFieldsValue(editingTask);
  }, [editingTask, form]);

  return (
    <Modal
      forceRender={true}
      onCancel={onCancel}
      onOk={onOk}
      okText={"确认"}
      cancelText={"取消"}
      confirmLoading={editLoading}
      title={"编辑任务"}
      visible={!!editingTaskId}
    >
      <Form initialValues={editingTask} {...layout} form={form}>
        <Form.Item
          label={"人物名"}
          name={"name"}
          rules={[{ required: true, message: "请输入人物名" }]}
        >
          <Input />
        </Form.Item>
        <Form.Item label={"经办人"} name={"processorId"}>
          <UserSelect defaultOptionName={"经办人"} />
        </Form.Item>
        <Form.Item label={"类型"} name={"typeId"}>
          <TaskTypeSelect defaultOptionName="类型" />
        </Form.Item>
      </Form>
      <div
        style={{
          textAlign: "right",
        }}
      >
        <Button
          size={"small"}
          style={{ fontSize: "14px" }}
          onClick={startDelete}
        >
          删除
        </Button>
      </div>
    </Modal>
  );
};
